@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #b7b7a4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* @import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300&display=swap'); */

.App {
  /* text-align: center; */
  /* font-family: 'Cormorant Garamond', serif; */
}

h1 {
  text-align: center;
}

.Navbar {
  display: flex;
  flex-direction: row;
  padding: 1em;
  background-color: #6b705c;
}

a {
  color: #ffe8d6;
  text-decoration: none;
  margin-right: 1em;
}

.active-link {
  color: #b7b7a4;
}

.Home {
  font-family: 'Reggae One', cursive;
  color: #6b705c;
}

.Home-image {
  margin: auto;
  margin-top: 3em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Home-image img {
  width: 25%;
  height: 25%;
}

.Home-buttons {
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 2em;
}

.Home-buttons a {
  color: #6b705c;
}

.Create {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1em;
}

.Create-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}
.Create-2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.Create-2 img {
  width: 70%;
  height: 70%;
}

form {
  margin-top: 5em;
}

.Form-group {
  display: flex;
  flex-direction: column;
}

.Form-group label {
  margin-bottom: 1em;
  font-family: 'Reggae One', cursive;
  color: #6b705c;
}

.Form-group input {
  margin-bottom: 1em;
  padding: 1em;
  width: 30em;
  outline: none;
  border: none;
}

.Form-group textarea {
  margin-bottom: 1em;
  padding: 1em;
  width: 30em;
  height: 10em;
  outline: none;
  border: none;
}

#submit {
  padding: 0.5em;
  border: none;
  outline: none;
  width: 30%;
  background-color: #6b705c;
}

.View-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 1em;
  width: 80%;
  margin: auto;
  margin-top: 5em;
  height: 100%;
}

.User-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #f8edeb;
  font-family: 'Playfair Display', serif;
}

.User-avatar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  margin-top: 2em;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  padding: 0.5em;
  background-color: #d8e2dc;
}

.User-avatar img {
  width: 100%;
}

.User-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 1em;
}
.User-details span {
  margin-bottom: 1em;
}

.User-conf {
  background-color: #ffe5d9;
  width: 100%;
  height: 100%;
  padding-top: 1em;
  padding-bottom: 1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.User-conf button {
  border: none;
  outline: none;
  padding: 1em;
  width: 70%;
  background-color: #f8edeb;
}

.User-qr-code {
  margin-top: 2em;
  margin-bottom: 2em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.User-qr-code span {
  margin-top: 1em;
}

.User-qr-image {
  border: 10px solid #ffe5d9;
  padding: 0.5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

